@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;700&family=Staatliches&display=swap");
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: rgb(220, 234, 247);
  font-family: "Raleway", sans-serif;
  font-weight: 500;
}

html,
body,
#root,
.container {
  height: 100%;
}
/* ------------ loading ---------- */
.loader {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loader__text {
  border-right: 0.15em solid #fff;
  letter-spacing: 0.15em;
  animation: blink-caret 0.7s step-end infinite;
}
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #fff;
  }
}

/* ------------ movies ---------- */
.movies {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.movie {
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly;
  margin: 50px 10px;
  padding: 20px;
  width: 40%;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.2);
}
.movie:hover {
  transform: scale(1.02);
}
.movie__poster {
  position: relative;
  top: -50px;
  margin-right: 30px;
  max-width: 150px;
  height: auto;
  box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.2);
}
.movie__data {
  width: 70%;
}
.movie__title {
  margin: 10px 0 0;
  font-family: "Staatliches", cursive;
  font-size: 24px;
}
.movie__year {
  display: inline;
  margin: 15px 0 0;
  font-size: 18px;
  font-weight: 700;
}
.movie__rating {
  margin-left: 10px;
  font-size: 14px;
  color: rgb(214, 154, 0);
}
.movie__genres {
  display: flex;
  margin: 10px 0 0;
  padding: 0;
  color: rgb(119, 119, 119);
}
.genres__genre {
  margin: 0 5px 0 0;
  list-style: none;
  font-size: 16px;
}
.genres__genre::after {
  content: " · ";
}
.genres__genre:last-child::after {
  display: none;
}
.movie__summary {
  margin: 10px 0 0;
  font-size: 14px;
  color: rgb(119, 119, 119);
  line-height: 150%;
}
